<template>
  <div class="cronometro">
    <span class="relogio">
      {{ relogioFormatado(atual) }}
    </span>
    <img :src="botoes ? play:restart " class='botoes' @click="chamarRelogio(botoes?'iniciar':'recomecar')"/>

    
    
  </div>
</template>
<script>

export default {
  props:['inicio','rodando','atual','play','restart'],
  data() {
    return {
      relogio: this.inicio,
      emAndamento:true,
      botoes:true,
         
            
    };
  },
  methods: {
    chamarRelogio:function(x){
     if(x=='iniciar'){
       this.$emit('eventoRelogio',{tarefa:'iniciar'})
     }
     else{
       this.$emit('eventoRelogio',{tarefa:'recomecar'})
     }
 
      this.botoes=!this.botoes 
    },
     
    relogioFormatado: function (seconds) {
      let minutos = Math.trunc(seconds / 60);
      let segundos = seconds%60
      if(minutos<10){
        minutos=`0${minutos}`
      }

      if(segundos<10){
        segundos=`0${segundos}`
      }
      return `00:${minutos}:${segundos}`;
    },
   
  },
  watch:{
      atual: function(x){
        
        if(x==this.inicio){
          this.botoes=true;
        }
      }
    },
  

};
</script>
<style scoped>
.cronometro {
  display: flex;
flex-direction: row;
justify-content: center;
align-items: center;
vertical-align: middle;
margin:2%;

}
.invisivel{
  display: none;
}
.relogio{
  font-family: 'Orbitron', sans-serif;
  font-weight: bold;
  font-size: 2rem;
}
img{
  width:2rem;
  height: 2rem;
  border-radius: 50%;
  margin-left:.5rem;
  background-color:green;
  vertical-align: center;
}
img:hover{
  cursor:pointer;
}
</style>


