<template>
    <section>
        <p class='autor'>{{ autor }} </p>
        <ul class='redes'>
            <p class='lista-titulo'>Redes sociais</p>
            <li v-for="(i,index) in redes" :key="index">
                <a :href="i.link" target="_blank"> {{ i.nome }}</a>
            </li>
        </ul>
        <ul class='projetos'>
            <p class='lista-titulo'> Outros projetos </p>
            <li v-for="(i,index) in projetos" :key="index">
                <a :href="i.link" target="_blank"> {{ i.titulo }}</a>
            </li>
        </ul>
    </section>
</template>
<script>
export default {
    data() {
       return{
           autor:'Desenvolvido por Leandro Carvalho',
           redes:[
              {
               nome:'Github',
               link:'https://github.com/leandro-crv',
               logo:'https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png'
           },
            {
               nome:'Linkedin',
               link:'https://www.linkedin.com/in/leandro-carvalho-87173123/',
               logo:'https://cdn.icon-icons.com/icons2/2699/PNG/512/linkedin_logo_icon_170234.png'
           }
           ],    
           projetos:[
               {
                   titulo:'Super Trunfo Países',
                   link:'https://leandro-crv.github.io/super-trunfo-paises/'
               },
               {
                   titulo:'Pódio Olímpico',
                   link:'https://leandro-crv.github.io/podio-olimpico/'
               }
           ]
       } 
    },
}
</script>
<style scoped>
section{
    background-color: gray;
    color:whitesmoke;
    margin-top:1rem;
    padding: 1em;
    border-top:solid black thick;
}

.autor{
    margin:.2rem;
}

ul{
    margin:.2rem;
    display:inline-block;
    width:40%;
    vertical-align: top;
}

li{
    list-style: none;
    display: inline-block;
    margin-right: 1rem;
}

.lista-titulo{
    font-weight: bold;
}

a{
    color:whitesmoke;
    text-decoration: underline;
    
}

a:hover{
    color:black;
}
</style>


