<template>
   <div id='resultado' class='resultado'>
      <h2>Fim de jogo!</h2>
      <div v-if="m.gabaritou">
        <p class='gabaritou'>{{m.acertosMsg}}</p>
      </div>
      <div v-else>
        <p>{{ m.acertosMsg }}</p>
        <p>{{ m.faltaramMsg}}</p>
      <div class='botoes'>
        <button id='mostrarSim' class="btn btn-secondary btn-lg" @click="clique(true)">Mostrar todos países</button>
        <button id='mostrarNao' class="btn btn-secondary btn-lg" @click="clique(false)">Tentar novamente</button>
      </div>
      </div>

    </div>
</template>
<script>
export default {
    props:['m'],
    methods:{
        clique:function(x){
            x ? this.$emit('sair',{mostrar:true}) : this.$emit('sair',{mostrar:false})
        }
    }
}
</script>
<style scoped>

#resultado{
width: 50vw;
min-height:50vh;
background: lightgray;
border:black solid thick;
border-radius:20px;
display: flex;
flex-direction: column;;
justify-content: center;
align-items: center;
padding:.2rem;
margin:2rem auto;
}

 button{
    margin:.7rem
} 
</style>


