<template>
  <section class='resultados-pais'>
    <p class='titulo-esporte'>{{ tituloTabela }} </p>
    <ul class="encontrados">
      <span :class="encontrados.length > 0 ? 'rotulo' : 'invisivel'"
        >Acertou:
      </span>
      <li v-for="(e, index) in encontrados" :key="index" class='encontrados-item'>
        {{ e }}
      </li>
    </ul>
   
    <hr />
    <ul class='lista'>
      <li class='lista-item' v-for="(p,index) in lista" :key="index">
        <section class='sede'>
           
           <!--  <img :src="p.logo" :alt="p.sede" class="logo"/> -->
            <p>{{ p.sede }} - {{ p.ano }}</p>
        </section>
        <section class='pais' :name="p.ouro">
            <img :src="buscarBandeira(p.ouro)" :alt="p.ouro" class="bandeira"  :title="p.ouro"/>
            <p class='legenda-pais' >{{ p.ouro }} </p>
        </section>
        <section class='pais' :name="p.prata">
            <img :src="buscarBandeira(p.prata)" :alt="p.prata" class="bandeira" :title="p.prata"/>
            <p class='legenda-pais'>{{ p.prata }} </p>
        </section>
        <section class='pais' :name="p.bronze">
              <img :src="buscarBandeira(p.bronze)" :alt="p.bronze" class="bandeira" :title="p.bronze" />
             <p class='legenda-pais'>{{ p.bronze }} </p>
        </section>

      </li>
    </ul>


  </section>
</template>
<script>


import edicoes from "../database/edicoes.js";
import codigos from "../database/codigos";
import $ from "jquery";
export default {
  props: ["encontrados",'mostrar','banco',"tituloTabela"],
  data() {
    return {
      lista:[],
       
      
    };
  },
  methods: {
    buscarBandeira: function (nome) {
      return codigos[codigos.findIndex((element) => element.nomePais == nome)]
        .bandeira;
    },
    },
           
    watch:{
        encontrados:function(x){
           
            if(x.length==0){
                [...$('.pais')].map((p)=> $(p).css('opacity','0'))
                
            }
            else{
            
                   
            let nomePais = [...document.querySelectorAll(".pais")];
           
             let filtro = nomePais.filter((element) => this.encontrados.includes(element.getAttribute('name'))); 
             
             filtro.map((f)=>{
                
                 $(f).css('opacity','1') 
             }) 
             

            }


        },
        mostrar:function(x){
            
            if(!x){
                return
                     
            }
            else{
           
            let nomePais = [...document.querySelectorAll(".pais")];
             let filtro = [...nomePais.filter((element) => !this.encontrados.includes(element.getAttribute('name')))]; 
             
             filtro.map((f)=>{
                 $(f).css('opacity','0.7')
                   

                 
             });
             
            }
        },
        banco:function(z){
        
        let arrayAnos = [];
        z.map((a) => arrayAnos.push(a.ano));
        let anos = [...new Set(arrayAnos)];
        var novoArray = [];
        anos.map((a) => {
        let sede = edicoes[edicoes.findIndex((e) => e.ano == a)].sedePort;
        let logo = edicoes[edicoes.findIndex((e) => e.ano == a)].imagem;
        let ouro =
          z[
            z.findIndex((e) => e.ano == a && e.medalha == "ouro")
          ].pais.nome[0];
        let prata =
          z[
            z.findIndex((e) => e.ano == a && e.medalha == "prata")
          ].pais.nome[0];
        let bronze =
          z[
            z.findIndex((e) => e.ano == a && e.medalha == "bronze")
          ].pais.nome[0];
        novoArray.push({
          ano: a,
          sede,
          logo,
          ouro,
          prata,
          bronze,
        });
      });
      novoArray.sort((a,b)=> b.ano - a.ano)
      
      this.lista = novoArray;
      
      }

    },
    


};
</script>
<style scoped>
.encontrados li {
  display: inline-block;
  margin: 0.4rem 0 0 0.2rem;
  border: thin black solid;
  background-color: whitesmoke;
  border-radius: 0.8rem;
  padding: 0.5rem;
  font-size: 0.9rem;
} 

.resultados-pais{
  padding-bottom:1rem;
}



.encontrados-item {
  list-style: none;
  margin: 1rem;
}

.lista{
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  
}

.lista-item{
  width:20rem;
  background-color:lightgray;
  list-style: none;
   border:thin solid black;
  padding:.3rem;
  margin-top:.5rem;
  margin-right:.5rem;
  
}



.lista-item section{
   display: inline-block;
   padding: .8rem;
   padding-top:0px;
   margin: .3rem auto;
 
}
.lista-item section p{
  padding:0px;
  margin: .2rem 0;
  
}
.lista-item section:first-child{
  border-bottom: solid black thin;
  display: block;
  margin:0px;
  font-weight: bold;
}


.bandeira{
  width:3em;
}


.pais {
  opacity:0;
}
.invisivel {
  display:none;
}

.rotulo {
  font-weight: bold;
}

.legenda-pais{
  font-size: .7rem;
}

.titulo-esporte{
  font-weight: bold;
  font-size: 1.8rem;
}

</style>


