<template>
<div class='escolhaEsportes'>
    <ul class='masculino'>
        <p>Masculino</p>
        <li v-for="(esp,index) in masculino" :key="`masc${index}`" class='esportes'>
           <img :id="esp" :src="icones[index]" class='icone'  @click="enviarEsporte"/>
            <p>{{ nomesEsportes[index] }} </p>
        
        </li>
    </ul>
        <ul class='feminino'>
        <p>Feminino</p>
        <li v-for="(esp,index) in feminino" :key="`fem${index}`" class='esportes'>
           <img :id="esp" :src="icones[index]" class='icone'  @click="enviarEsporte"/>
            <p>{{ nomesEsportes[index] }} </p>
        
        </li>
    </ul>
    
</div>
    

</template>
<script>
import futebol from "../assets/futebol.png";
import volei from "../assets/volei.png";
import basquete from "../assets/basquete.png"
export default {
    methods:{
        enviarEsporte: function(y){
            let nome = y.target.id
            this.$emit('selectEsporte',{banco:nome})
        },


        },
    data(){
        return{
            icones:[basquete,futebol,volei],
            nomesEsportes: ["Basquete","Futebol","Vôlei"],
            masculino:["basqueteM","futebolM","voleiM"],
            feminino:["basqueteF","futebolF","voleiF"],
            selecionado:"Selecione o esporte"
        }
    },

}


</script>
<style scoped>
.escolhaEsportes{
    background: linear-gradient(gray,lightgray);
    
    
}

.esporte-selecionado{
    background-color:lightgray;
    font-size: 2rem;
      
    
}

ul{
   display: inline-block;
   
    padding:.7rem;
    margin:0 auto;
}

li{
    display: inline-block;
    list-style: none;
    margin:0 auto 0 1rem;
}

.icone{
    width:5rem;
    border:black solid thick;
    border-radius: 1rem;
    padding:.4rem;
}
.icone:hover{
    border:goldenrod solid thick;
}


</style>


