<template>
  <div id="app">
    <header>
      <header-header/>
    </header>
    <main>
    <esporte-esporte @selectEsporte="bancoEsporte($event)"/>
    <resultado-resultado v-show="mostrarResultado" :m="mensagemResultado" @sair="mostrar($event)"/>
    <cronometro-cronometro :inicio="cronometro.inicio" :atual="cronometro.atual" @eventoRelogio="controlarRelogio($event)" :play="play" :restart="restart"/>
    <input type='text' id='busca' @keyup="buscar" placeholder="digite um país" :disabled="!cronometro.rodando" >
    <placar-placar v-bind:acertos="paisesEncontrados.length" v-bind:total="paisesUnicos.length" v-bind:reset="reset"/>
   
    <pais-pais v-bind:encontrados="paisesEncontrados" v-bind:mostrar="mostrarTudo" v-bind:banco="banco" :tituloTabela="tituloEsporte"/>
    </main>
    <footer-footer/>
    
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Cronometro from "./components/Cronometro.vue";
import Pais from "./components/Pais.vue";
import removerAcentos from "../removerAcentos.js";
import $ from "jquery";
import Placar from "./components/Placar.vue";

import play from "./assets/play.svg";
import restart from "./assets/restart.svg";
import Resultado from "./components/Resultado.vue";
import Esporte from "./components/Esporte.vue";
import Footer from "./components/Footer.vue"
import pa from "../paisesAlternativos.js"



export default {
  name:'app',
  components:{
    "header-header":Header,
    "cronometro-cronometro":Cronometro,
    "pais-pais":Pais,
    "placar-placar":Placar,
    "resultado-resultado":Resultado,
    "esporte-esporte":Esporte,
    "footer-footer": Footer
  },
  data(){
    return{
      banco:[],
      paisesUnicos:[],
      paisesEncontrados:[],
      cronometro:{
        inicio:180,
        atual:180,
        rodando:false
      },
      reset:false,
      mostrarTudo:false,
      mensagemResultado:'',
      mostrarResultado:false,
      play,
      restart,
      tituloEsporte:"Clique nos ícones acima para selecionar o esporte"
      
      
      
    }
  },
  watch:{
    'cronometro.atual': async function(x){
       
       const wait = (ms) => new Promise(resolve => setTimeout(resolve, ms))
     
        if(x>0 && this.cronometro.rodando){
          await wait(1000)
          this.cronometro.atual--
       }
      
       
       if(x==0){
         this.fimDeJogo()
        } 

     
   
    },
    'cronometro.rodando':function(x){
      if(!x){
        setTimeout(()=>this.cronometro.atual=this.cronometro.inicio,1000)
      }
    },
    paisesEncontrados: function(x){
      if(x.length == this.paisesUnicos.length){
        
        this.fimDeJogo()
      }
    }
  },
  methods:{
    buscar:function(){
      let pesquisa = removerAcentos($('#busca').val())
      pa.map((p)=>{
        if(pesquisa.length==p.busca.length && pesquisa.match((new RegExp(p.busca,"gi")))){
          if(this.banco.findIndex(element => element.pais.nome[0] == p.retorno)!=-1){
            this.inserirPais(p.retorno)
          }
        }
      });

      if(pesquisa.match(new RegExp("Alemanha","gi"))){
        let alemanhas = ["Alemanha","Alemanha Ocidental","Alemanha Oriental","Equipe Unificada da Alemanha"];
        alemanhas.map((a)=>{
          let contem = this.banco.findIndex(element => element.pais.nome[0] == a)
          if(contem!=-1){
           this.inserirPais(a); 
          }
        })
      };

        this.banco.map((p)=>{
        let comprimento = p.pais.nome[0].length;
        let texto1 = removerAcentos(p.pais.nome[0])
        let condicao = pesquisa.length==comprimento && texto1.match(new RegExp(pesquisa,'gi'))!=null
        condicao ? this.inserirPais(p.pais.nome[0]) : ''
                   
      })   
    },
    inserirPais:function(pais){
      if(this.paisesEncontrados.findIndex(e => e == pais)==-1){
        this.paisesEncontrados.push(pais)
        this.paisesEncontrados.sort((a,b) => a.localeCompare(b))
      }
      $('#busca').val('')
      $('#busca').addClass('acertou')
      setTimeout(()=> $('#busca').removeClass('acertou'),300)
    },
    controlarRelogio:function(x){
      if(this.banco.length==0){
        alert('selecione um esporte')
        return
      }
      if(x.tarefa=='iniciar'){
        this.limparTabela()
        this.mostrarResultado=false;
        this.mostrarTudo = false;
        this.cronometro.rodando=true;
        setTimeout(()=> this.cronometro.atual = this.cronometro.atual-1,1000)
       
      }
      else if(x.tarefa=='recomecar'){
        this.cronometro.rodando=false;
        this.limparTabela()
        this.mostrarResultado=false;
      
      }
    },
    girarRelogio:function(){
      if(this.cronometro.atual > 0 && this.cronometro.rodando){
        this.cronometro.atual = this.cronometro.atual-1
        setTimeout(()=>this.girarRelogio(),1000)
      }
      
      else{
        this.fimDeJogo()
      }
    },
    fimDeJogo:function(){
      $('#busca').val('')
      this.cronometro.rodando=false;
      this.mensagemResultado = this.formatarMensagemResultado()
      this.mostrarResultado = true;
    },

    limparTabela:function (){
     
      this.paisesEncontrados = [];
      
    },
    mostrar: function(x){
     
     this.mostrarResultado = false
      if(x.mostrar){
        this.mostrarTudo=true;
      }
      else{
        
        this.limparTabela()
      }
      this.reiniciarCronometro()
    },
    formatarMensagemResultado:function(){
      let acertos = this.paisesEncontrados.length;
      let faltaram = this.paisesUnicos.length - acertos;
      let acertosMsg;
      let faltaramMsg;
      
      if(faltaram==0){
        return{
          gabaritou:true,
          acertosMsg:'Parabéns!!! Você acertou todos os medalhistas!!!'
        }
      }
     
      if(acertos==0){
        acertosMsg = 'Infelizmente você não acertou nenhum medalhista!';
      }
      else if(acertos==1){
        acertosMsg='Você acertou 1 país medalhista!';
      }
      else {
        acertosMsg=`Você acertou ${acertos} países medalhistas!`;
      }
      
    if(faltaram==1){
      faltaramMsg="Faltou apenas 1 país!"
    }
    else{
      faltaramMsg=`Faltaram ${faltaram} países!`
    }

     return{
       gabaritou:false,
       acertosMsg,
       faltaramMsg
     }
    },
  reiniciarCronometro:function(){
    this.cronometro.atual = this.cronometro.inicio
  },
  bancoEsporte:function (x){
    this.limparTabela();
    this.mostrarResultado = false;
    this.banco = require(`./database/${x.banco}.js`);
    this.listarPaisesUnicos();
    this.esporteSelecionado(x.banco)
    

  },
  esporteSelecionado:function (x){
    let genero = x[x.length-1];
    let esporte = x.split(genero)[0]
    genero=="M" ? genero = 'masculino' : genero = 'feminino'
    esporte = esporte[0].toUpperCase() + esporte.substr(1)
    if(esporte=="Volei"){
      esporte="Vôlei"
    }
   this.tituloEsporte = esporte + " " + genero
  },
  listarPaisesUnicos:function(){
    var unicos = [];
    this.banco.map((f)=>{
      unicos.push(f.pais.nome[0])
    })
    
    this.paisesUnicos = [...new Set(unicos)]
  }
  
  },


}

//emitir evento: https://www.youtube.com/watch?v=u--wUOQVdXg
</script>

<style>

#app{
  font-family: 'Montserrat', sans-serif;
  text-align:center;
  width:100%;
  height: 100vh;
  margin:0px;
 
}
#busca{
  /* width:20%; */
  border-radius: .5rem;
  height: 2rem;
}
.verde{
  background-color:green;
}

.acertou{
  background-color: lightgreen;
}

.mensagem{
  display:none;
 
  color:green;
}



</style>



