export default (texto)=>{
    texto = texto.replace(/[ÀÁÂÃÄÅ]/,"A");
    texto = texto.replace(/[àáâãäå]/,"a");
    texto = texto.replace(/[ÈÉÊË]/,"E");
    texto = texto.replace(/[èéê]/,"e");
    texto = texto.replace(/[í]/,'i');
    texto = texto.replace(/[ôóõ]/,'o');
    texto = texto.replace(/[úüû]/,'u')
    texto = texto.replace(/[Ç]/,"C");
    texto = texto.replace(/[ç]/,"c");
    return texto
}

